.navbar {
    position: absolute;
    background-color: #060b26;
    height: 100vh;
    width: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
    z-index: 11;
  }
  .map-menu{
    margin-top: 55vh;
    margin-left: 10px;
  }
  .map-menu-item{
    margin-right: 8px;
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 8px;
    list-style: none;
    height: 60px;
    /* height: 50px; */
    width: 50px;
  }
  .menu-bars {
    margin-left: 1rem;
    font-size: 1.5rem;
    background: none;
    z-index: 11;
  }
  .nav-menu {
    z-index: 11;
    background-color: #060b26;
    width: 70px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
  }
  .nav-text {
    margin-right: 8px;
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 8px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #2077ce;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }

